import React, { useState, useEffect } from 'react';
import { Row, Card, Form, Col, Input, Spin, message } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { SelectAjax } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import { getDistricts, getDistrictById } from 'services/district';
import { getListTown } from 'services/town';
import { getWardById, createWard, updateWard } from 'services/ward';
import { requiredRule } from 'utils/validateRules';

const TIME_RETURN = 1500;

const WardForm = props => {
  const [loading, setLoading] = useState(false);
  const { form } = props;
  const { getFieldDecorator, getFieldValue, setFieldsValue } = form;
  const townIdFieldValue = getFieldValue('townId');

  const handleSave = e => {
    e.preventDefault();
    form.validateFields(async (err, values) => {
      if (err) {
        return;
      }
      setLoading(true);
      const { id } = getQueryParams();
      try {
        if (id) {
          values.id = id;
          await updateWard(id, values);
          message.success('Cập nhật thành công');
        } else {
          await createWard(values);
          message.success('Tạo mới thành công');
        }
        setTimeout(() => props.history.goBack(), TIME_RETURN);
      } catch (error) {
        message.error(error.message.reason);
      }
      setLoading(false);
    });
  };

  const getTowns = () => {
    return getListTown({ isPagingEnabled: false });
  };

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 6 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 18 },
    },
    colon: false,
  };

  const initValue = async () => {
    const { id } = getQueryParams();
    if (id) {
      setLoading(true);
      const res = await getWardById(id);
      const { districtId } = res.data;
      const resDistrict = await getDistrictById(districtId);
      const { townId } = resDistrict.data;
      const values = res.data;
      values.townId = townId;
      form.setFieldsValue(values);
      setLoading(false);
    }
  };

  useEffect(() => {
    initValue();
  }, []);

  return (
    <Card title="WardForm">
      <Spin spinning={loading}>
        <Row>
          <Form {...formItemLayout} onSubmit={handleSave}>
            <Row>
              <Col span={12} offset={6}>
                <Form.Item {...formItemLayout} label="Tỉnh/thành">
                  {getFieldDecorator('townId')(<SelectAjax service={getTowns} />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={12} offset={6}>
                <Form.Item {...formItemLayout} label="Quận/huyện">
                  {getFieldDecorator('districtId', {
                    rules: [requiredRule],
                  })(
                    <SelectAjax
                      setFieldsValue={setFieldsValue}
                      service={getDistricts}
                      params={{ townId: townIdFieldValue }}
                      disabled={!townIdFieldValue}
                      allowGetData={!!townIdFieldValue}
                    />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={12} offset={6}>
                <Form.Item {...formItemLayout} label="Phường/xã">
                  {getFieldDecorator('label', {
                    rules: [requiredRule],
                  })(<Input />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={12} offset={6} style={{ textAlign: 'center' }}>
                <div className="btn-group">
                  <BtnSave htmlType="submit" />
                  <BtnCancel onClick={() => props.history.goBack()} />
                </div>
              </Col>
            </Row>
          </Form>
        </Row>
      </Spin>
    </Card>
  );
};

export default Form.create()(props => (
  <Page {...props}>
    <WardForm {...props} />
  </Page>
));



